import { useEffect, useState } from "react"
import webSocketService from "../api/WebSocketService";
import VideoPlayer from "./VideoPlayer";

export default function User() {
    const [videoid, setVideoid] = useState(null)
    
    

 
    useEffect(() => {

        function onconnected() {
            console.log("connected to websocket")
            webSocketService.stompClient.subscribe('/topic/group-state', (message) => {
                    setVideoid(JSON.parse(message.body).videoId)
                    
            })
        }
        webSocketService.connect(onconnected);
    }, [])
    return <>
        <h2>Heloo User welcome to my world</h2>
        { videoid==null && <h4>Please ask Admin to search or select a video</h4>}
        { videoid && <VideoPlayer videoId={videoid} />}
        </>
}