
import YouTube from 'react-youtube';
import webSocketService from '../api/WebSocketService';

export default function AdminVideoPlayer({videoId})
{
    const opts = {
        height: '400px',
        width: '100%'
    
      }
    const onPlay=(e)=>{
        
        webSocketService.stompClient.publish({
          destination: '/app/group/update',
          body:JSON.stringify({
            groupId:"temp123",
            videoId:videoId,
            timestamp:Date.now(),
            isPlaying:true
          }),
        });
          }
          const onPause=(e)=>{
            webSocketService.stompClient.publish({
                destination: '/app/group/update',
                body:JSON.stringify({
                  groupId:"temp123",
                  videoId:videoId,
                  timestamp:Date.now(),
                  isPlaying:false
                }),
              });
          }


    return (
        <div>
            { videoId == null && <h4>Search  a Video ....</h4>}
          
            { videoId && <YouTube videoId={videoId} opts={opts}  onPlay={onPlay} onPause={onPause}/>}
          
        </div>
      );
}