import { useEffect, useState } from "react";
import SearchBar from "./SearchBar";
import VideoList from "./VideoList";
import VideoPlayer from "./VideoPlayer";
import { fetchVideos } from '../api/youtube';
import webSocketService from "../api/WebSocketService";
import AdminVideoPlayer from "./AdminVideoPlayer";

export default function Admin()
{

    const [videos, setVideos] = useState([]);
    const [selectedVideo, setSelectedVideo] = useState(null);
    const [videoid,setVideoid]=useState(null)
    
 
    const handleSearch = async (query) => {
      const results = await fetchVideos(query);
      setVideos(results);
      setSelectedVideo(results[0]); 
      setVideoid(results[0].id.videoId)
      console.log(webSocketService.stompClient.connected)

     
      // when searched and first videoid is sent to backend

      webSocketService.stompClient.publish({
                destination: '/app/group/update',
                body:JSON.stringify({
                    groupId:"temp123",
                    videoId:results[0].id.videoId,
                    timestamp:Date.now(),
                    isPlaying:false
    
                }),
            });
      
      
    
     
      
    };

    useEffect(()=>{
  webSocketService.connect(()=>{console.log("connected to websocket")});
    },[])
    
    return<>
    <h2>boss {videoid}</h2>
    <SearchBar onSearch={handleSearch}/> 
    
    <AdminVideoPlayer videoId={videoid}/>
    <VideoList videos={videos} onVideoSelect={setVideoid}/>
    </>
}